.milk {
  background-color: var(--background-about);
  font-family: $mainFont; 
  &__image {
    margin-right: 0;
  }
  }
.about {
  &__title {
    padding-top: 125px;
    text-align: center;
    color: var(--brand-darker);
    font-size: large;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.14;
    font-family: $secondaryFont;
  }
  &__title2 {
    padding-top: 15px;
    text-align: center;
    color: var(--brand-the-darkest);
    font-size: large;
    font-weight: 400;
    font-size: 58px;
    line-height: 1.14;
    font-family: $secondaryFont;
  }
  &__description {
    color: black;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.76;
    padding-bottom: 35px;
  }
  &__description2 {
    color: var(--text-main);
    font-weight: 500;
    font-size: 14px;
    line-height: 1.94;
    padding-bottom: 45px;
  }
  &__content {
    padding-top: 55px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }
  &__text {
    padding-left: 0;
    max-width: 463px;
  }
  &__button{
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--brand-the-darkest);

    background-color: white;
    border-radius: 22px;
    border-style: none;
    width: 176px;
    height: 44px;
    &-icon{
      margin-left: 10px;
    }
    &:hover{
      cursor: pointer;
      background-color: var(--background-button-light-accent);
    }
  }
  }
.advantages {
  &__title-hidden {
    display: none;
  }
  &__title{
    padding-top: 40px;
    font-size: 58px;
    font-weight: 400;
    line-height: 1.14;
    color: var(--brand-the-darkest);
    font-family: $secondaryFont;
  }
  &__list {
    padding-top: 39px;
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: center;
  }
  &__text{
    max-width: 330px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.76;
    color: black;
    padding-top: 15px;
    padding-bottom: 310px;
  }
  &__icon{
    height: 50px;
  }
}
@media screen and (max-width: 1280px){
  .milk__image{
    max-width: 340px;
  }
  .about{
    &__text{
      max-width: 340px;
    }
    &__title{
      font-size: 26px;
    }
    &__title2{
      font-size: 48px;
    }
    &__description{
      font-size: 14px;
    }
  }
  .advantages{
    &__list{ 
      justify-content: space-around;
      & li{
      max-width: 180px;
    }
  }
    &__title{
      font-size: 48px;
    }
    &__text{
      font-size: 14px;
    }
  }
}
@media screen and (max-width: 768px){
  .milk__image{
    max-width: 280px;
  }
  .about{
    &__content{
      flex-direction: column;
    }
    &__title{
      font-size: 18px;
    }
    &__title2{
      font-size: 34px;
    }
    &__description{
      font-size: 12px;
    }
  }
  .advantages{
    &__list{
      flex-direction: column;
    justify-content: center;
      & li{
      max-width: 230px;
    }
  }
    &__title{
      font-size: 36px;
    }
    &__text{
      font-size: 12px;
      padding-bottom: 38px;
    }
  }
}